import { Component } from '@angular/core';
import { CategoryService } from '../shared/category.service';
import { AuthService } from '../shared/auth.service';
import { PackageService } from '../shared/package.service';

import { ToastrService } from 'ngx-toastr';

import { DialogService } from "ng2-bootstrap-modal";
import { ConfirmPopupComponent } from '../confirm-popup/confirm-popup.component';

@Component({
    moduleId: module.id,
    selector: 'admin',
    templateUrl: 'admin.component.html',
    styleUrls: ['admin.component.scss']
})
export class AdminComponent {
    categories: any[] = [];
    rootCategories: any[] = [];
    subCategories: any[] = [];
    pathCategories: any[] = [];

    // mailing service credentials
    smtpUser: string;
    smtpPass: string;
    smtpMailer: string;
    smtpHost: string;
    smtpPort: number;
    smtpType: any = 1;
    smtpService: string;

    isLoading: boolean = false;
    editing: any = {};

    selectedCategory: any;
    newCatName: any = {};
    categoryType: string = 'Root Category';

    newCategoryName: string;

    constructor(private categoryService: CategoryService, 
        private toastr: ToastrService, 
        private auth: AuthService,
        private dialogService: DialogService,
        private packSer: PackageService) {
        this.isLoading = true;
        this.categoryService.getRootCategory()
            .subscribe(data => {
                this.isLoading = false;
                if (data.success) {
                    this.categories = data.categories;
                    this.rootCategories = data.categories;
                    console.log(this.categories);
                } else {
                    this.toastr.error(data.msg, 'Error');
                }
            }, error => {
                this.isLoading = false;
            });
    }

    ngOnInit() {
        this.isLoading = true;
        this.categoryService.getSmtp().subscribe( res => {
            this.isLoading = false;
            if(res.success) {
                if(!res.user) return this.toastr.error('No SMTP configuration has been found. Please enter SMTP details.', 'Error');
                this.smtpMailer = res.mailerId;
                this.smtpUser = res.user;
                if(res.host) {
                    this.smtpHost = res.host;
                    this.smtpPort = res.port;
                    this.smtpType= 2;              
                } else {
                    this.smtpType = 1;
                    this.smtpService = res.service;
                }
            } else {
                this.toastr.error(res.msg, 'Error');
            }
        }, error => {
            this.isLoading = false;
        })
    }

    createNewCategory() {
        this.isLoading = true;
        let catName = this.newCategoryName;
        let parentId = this.selectedCategory? this.selectedCategory._id: null;
        if(parentId) {
            this.categoryService.addSubCategory(parentId, catName)
                .subscribe( data => {
                    handleCatAddition(data, this.categories);
                }, error => {
                    this.isLoading = false;
                })
        } else {
            this.categoryService.addRootCategory(catName)
                .subscribe(data => {
                    handleCatAddition(data, this.rootCategories);
                }, error => {
                    this.isLoading =false;
                })

        }
        let handleCatAddition = (data, categories) => {
            this.isLoading = false;
            this.newCategoryName = '';
            if (data.success) {
              console.log(data.category);
                categories.push(data.category);
                console.log(categories);
            } else {
                this.toastr.error(data.msg, 'Error')
            }
        }
    };

    // getSubCategories(category) {
    //     let id = category._id;
    //     this.categories = [];
    //     this.populatePath(category);
    //     this.categoryService.getSubCategory(id)
    //         .subscribe(data => {
    //             if (data.success) {
    //                 this.categories = data.category.childcategories;
    //             }
    //         });
    // };

    goToRootCats() {
        this.pathCategories = [];
        this.categoryType = 'Root Category';
        this.categories = this.rootCategories;
        this.selectedCategory = null;
    }

    getSubCats(cat) {
        console.log(typeof(cat.childcategories));
        this.populatePath(cat);        
        if(typeof(cat.childcategories) != undefined) {
            this.categories = cat.childcategories;
            this.categoryType = 'Sub-category';
            return;
        }
        this.isLoading = true;
        this.categoryService.getSubCategory(cat._id)
            .subscribe( res => {
                this.isLoading = false;
                this.categoryType = 'Sub-category';
                if(res.success) {
                    this.categories = res.category.childcategories;
                } else {
                    this.toastr.error(res.msg, 'Error');
                }
            }, error => {
                this.isLoading = false;
            })
    }

    removeCategory(ct, index) {
        if (ct) {
            let categoryname = ct.categoryname;
            let id = ct._id;
            this.isLoading = true;
            this.categoryService.removeCategory(id, true)
                .subscribe(data => {
                    this.isLoading = false;
                    if (data.success) {
                        this.categories.splice(index, 1)
                        console.log('Deleted Successfully');
                    } else {
                        if(data.sessions.length) {
                            this.showConfirm('Confirm Delete', data.msg).subscribe( res => {
                                if(res) {
                                    this.categoryService.removeCategory(id, false, data.catIds, data.sessions)
                                        .subscribe(res => {
                                            if(res.success) {
                                                this.categories.splice(index, 1);
                                            } else {
                                                this.toastr.error(res.msg, 'Error')
                                            }
                                        });
                                }
                            });
                        } else {
                            this.toastr.error(data.msg, 'Error');
                        }
                    }
                }, error => {
                    this.isLoading = false;
                });
        }
    }

    startEdit(cat) {
        this.editing[cat._id] = true;
        this.newCatName[cat._id] = cat.categoryname;
    }

    renameCategory( catId, index) {
        let category = { newcategoryname: this.newCatName[catId] };
        this.isLoading = true;
        this.categoryService.updateCategory(category, catId)
            .subscribe(data => {
                this.isLoading = false;
                if (data.success) {
                    this.categories[index].categoryname = this.newCatName[catId];
                    delete this.newCatName[catId];
                    console.log(this.newCatName);
                    this.editing[catId] = false;
                    console.log('Updated Successfully');
                } else {
                    this.toastr.error(data.msg, 'Error')
                }
            }, error => {
                this.isLoading = false;
            });
    }

    populatePath(ct) {
        let selectedIndex = this.pathCategories.indexOf(ct);
        if (selectedIndex != -1) {
            this.selectedCategory = this.pathCategories[selectedIndex];
            this.pathCategories.splice(selectedIndex + 1, this.pathCategories.length - selectedIndex);
        } else {
            this.selectedCategory = ct;
            this.pathCategories.push(ct);
        }
        console.log(this.pathCategories);
    }

    populateRootCategories() {
        this.categories = this.rootCategories;
        this.pathCategories = [];
    }

    selectSmtpType(type) {
        console.log('select smtp type', type);
        this.smtpType = type;
    }

    storeMailingCredentials () {
        let credentials;
      if(this.smtpType === 1) {
          credentials = {
            smtpUser: this.smtpUser,
            smtpPass: this.smtpPass,
            smtpMailer: this.smtpMailer,
            smtpService: this.smtpService
          }
      } else {
        credentials = {
            smtpUser: this.smtpUser,
            smtpPass: this.smtpPass,
            smtpMailer: this.smtpMailer,
            smtpHost: this.smtpHost,
            smtpPort: this.smtpPort
        }
      }
      this.categoryService.storeMailing(credentials).subscribe( res => {
        if(res.success){
          this.toastr.success('SMTP options saved!', 'Success');
        } else {
          this.toastr.error(res.msg || 'There was an error', 'Error');
        }
      })
    }

  showConfirm(title, message) {
    let popup = this.dialogService.addDialog(ConfirmPopupComponent, {
      title: title,
      message: message
    })
    return popup;
  }

    //package implementation
    packList: any[];

    changeAdminTab(e) {
        console.log(e);
        if(e.nextId == 'user_packages') {
            this.getPackList();
        }
    }

    getPackList() {
        this.isLoading = true;
        this.packSer.getAllPackages().subscribe( res => {
            this.isLoading = false;
            if(res.success) {
                this.packList = res.packages;
            } else {
                this.toastr.error(res.msg, 'Error');
            }
        }, error => {
            this.isLoading = false;
        })        
    }

    removePackage(pack, index) {
        this.isLoading = true;
        this.packSer.removePackage(pack._id).subscribe(res => {
            this.isLoading = false;
            if(res.success) {
                this.toastr.success('Package deleted successfully!', 'Success')
                this.packList.splice(index, 1);
            } else {
                this.toastr.error(res.msg, 'Error')
            }
        }, error => {
            this.isLoading = false;
        })
    }

}



