import { Component, OnInit } from '@angular/core';

import { ImageService } from '../shared/image.service';
import { VgAPI } from 'videogular2/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { ToastrService } from 'ngx-toastr';

import {environment} from '../../environments/environment';

@Component({
    moduleId: module.id,
    selector: 'slideshow',
    templateUrl: 'slideshow.component.html',
    styleUrls: ['slideshow.component.scss']
})
export class SlideshowComponent implements OnInit {

    title = 'Session Slildeshow Page';
    currentUser = localStorage.getItem('uname');
    visibleImages: any[] = [];
    totalSessions: any[];
    currentIndex = 0;
    imageSlideTime: Number = 2000;
    imageTitle: String;
    vPlayer = true;
    param: any;

    listenSub: any;

    // baseUrl: string = 'http://localhost:8080/';
  // baseUrl: string = 'https://ionic-node-auth.herokuapp.com/';
  baseUrl: string = environment.baseUrl + '/';

    public times = [
        { value: 2000, display: '2 Sec' },
        { value: 4000, display: '4 Sec' },
        { value: 6000, display: '6 Sec' }
    ];

    constructor(private imageService: ImageService, 
        public api: VgAPI, 
        private route: ActivatedRoute,
        private router: Router,
        private _location: Location,
        private toastr: ToastrService) {
    }

    ngOnInit() {
        this.getParamValues();
    }

    getParamValues() {
        this.route.params.subscribe(params => {
            this.param = params['id'];
            this.startListen();
        });
    }

    startListen() {
        if(this.imageService.qrSecret) {
            this.listenSub = this.imageService.listenSessionStop().subscribe(data => {
                if(data.secret === this.imageService.qrSecret) {
                    this.stopPlayer();
                    this._location.back();
                }
            })
        }        
    }


    onPlayerReady(api: VgAPI) {
        this.api = api;
        if (this.param) {
            this.startSlideShow(this.param);
        }
    }


    startSlideShow(sessionId) {
        this.api.fsAPI.toggleFullscreen();
        if (sessionId) {
            this.imageService.getImages(sessionId)
                .subscribe(data => {
                    if (data.success) {
                        console.log(data.files);
                        if(!data.files.length) {
                            this.router.navigate(['/gallery'])
                            return this.toastr.error('No files in this session!', 'Error');
                        }
                        this.visibleImages = data.files;
                        this.slideshow();
                    } else {
                        this.toastr.error(data.msg, 'Error');
                    }
                }, error => {

                });
        }
    }

    slideshow() {
        var myVideo = document.getElementsByTagName('video')[0];
        var myImage = document.getElementsByTagName('img')[0];

        this.currentIndex = 0;
        if (this.visibleImages[this.currentIndex].imagetype == 'image') {
            let curImage = this.visibleImages[this.currentIndex];
            this.vPlayer = false;
            myImage.src =  this.baseUrl +'session/stream_files?file=' + this.visibleImages[this.currentIndex].imagename;
            this.imageTitle = this.visibleImages[this.currentIndex].imagetitle;
            let titleAlign = this.visibleImages[this.currentIndex].titleAlign;
            console.log('alignment: ', titleAlign)
            window.document.querySelector('.image-text').classList.add(titleAlign);
            window.document.querySelector('.image-text').setAttribute('style', 'font-size: '+this.visibleImages[this.currentIndex].titleSize+'px');            
            setTimeout(() => {
                this.myAddListener();
            }, curImage.imagedelay*1000 || this.imageSlideTime);
        } else {
            this.vPlayer = true;
            myVideo.src = this.baseUrl +'session/stream_files?file=' + this.visibleImages[this.currentIndex].imagename;
            myVideo.load();
        }
    }

    myAddListener() {
        // console.log('nextItem');
        var myVideo = document.getElementsByTagName('video')[0];
        var myImage = document.getElementsByTagName('img')[0];
        if(this.visibleImages[this.currentIndex].imagetype === 'image')
            window.document.querySelector('.image-text').classList.remove(this.visibleImages[this.currentIndex].titleAlign);
        this.currentIndex = (this.currentIndex + 1) % this.visibleImages.length;

        if (this.visibleImages.length) {
            if (this.visibleImages[this.currentIndex].imagetype == 'image') {
              window.document.querySelector('.image-text').classList.add(this.visibleImages[this.currentIndex].titleAlign);
              let curImage = this.visibleImages[this.currentIndex];
                this.vPlayer = false;
                myImage.src =  this.baseUrl +'session/stream_files?file=' + this.visibleImages[this.currentIndex].imagename;
                this.imageTitle = this.visibleImages[this.currentIndex].imagetitle;
                setTimeout(() => {
                    this.myAddListener();
                }, curImage.imagedelay*1000 || this.imageSlideTime);
            } else {
                this.vPlayer = true;
                myVideo.src =  this.baseUrl +'session/stream_files?file=' + this.visibleImages[this.currentIndex].imagename;
            }
        }
    }

    stopPlayer() {
        this.visibleImages = [];
        var myImage = document.getElementsByTagName('img')[0];
        var myVideo = document.getElementsByTagName('video')[0];
        myImage.src = myVideo.src = 'assets/img/hqdefault.jpg';
    }

    ngOnDestroy() {
        if(this.listenSub) {
            this.listenSub.unsubscribe();
        }
    }

}



