import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { ImageService } from '../shared/image.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'session-qr',
  templateUrl: './session-qr.component.html',
  styleUrls: ['./session-qr.component.scss']
})
export class SessionQrComponent implements OnInit {

  constructor(private imageService: ImageService,
      private toastr: ToastrService,
      private router: Router) { }
  sessions: any[] = [];
  qrCodes: any[] = [];
  checkInterval:any;
  checkSub: any;
  JSON = JSON;

  isLoading: boolean = false;
  qrSecret = this.uuidv4();

  ngOnInit() {
    this.populateSessions();

    this.checkSub = this.imageService.qrScanCheck().subscribe( data => {
      console.log('qr scan check: ', data);
      if(data.sessionId && data.secret == this.qrSecret) {
        let scannedSession = this.sessions.find(session => session._id == data.sessionId)
        this.imageService.qrSecret = this.qrSecret;
        console.log('starting slideshow');
        this.startSlideShow(scannedSession);
      } 
    })    

    this.imageService.qrSecret = this.qrSecret;
  }

  uuidv4() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
      var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  }  

  ngOnDestroy() {
    if(this.checkInterval) {
      clearInterval(this.checkInterval);
    }
    if(this.checkSub) {
      this.checkSub.unsubscribe();
    }
  }

  populateSessions() {
    this.isLoading = true;
    this.imageService.getAllSessions().subscribe( res => {
      this.isLoading = false;
      if(res.success) {
        this.sessions = res.data;
        this.sessions.forEach((value, index) => {
          this.qrCodes.push({userId: value.userId, sessionId: value._id, sessionname: value.sessionname, secret: this.qrSecret});

        });
      } else {
        this.toastr.error('There was an error', 'Error')
      }
    }, error => {
      this.isLoading = false;
    })
  }

  startSlideShow(session) {
    this.router.navigate(['/slideshow', session._id]);
  }



}



