import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { JsonPipe } from '@angular/common';
import { ImageService } from '../shared/image.service';
import { CategoryService } from '../shared/category.service';
import { Observable } from 'rxjs/Rx';
import { DndModule } from 'ng2-dnd';
import { QRCodeComponent } from 'angular2-qrcode';
import { AuthService } from '../shared/auth.service';

import { DialogService } from "ng2-bootstrap-modal";
import { ConfirmPopupComponent } from '../confirm-popup/confirm-popup.component';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.scss']
})
export class GalleryComponent implements OnInit {

  isLoading: boolean = false;

  title = 'Recent Media Files';
  currentUser = this.auth.getCurrentUser().username;
  // visibleImages: any[] = [];
  totalSessions: any[];
  catones: any[] = [];
  cattwos: any[] = [];
  catthrees: any[] = [];
  JSON = JSON;
  catone: any;
  cattwo: any;
  catthree: any;
  //qr codes
  qrCodes: any[] = [];
  qrSecret: string = this.uuidv4();

  constructor(private imageService: ImageService, public router: Router,
    private categoryService: CategoryService,
    private auth: AuthService,
    private dialogService: DialogService,
    private toastr: ToastrService) {
    this.isLoading = true;
    this.categoryService.getRootCategory()
      .subscribe(data => {
        this.isLoading = false;
        if (data.success) {
          this.catones = data.categories;
        } else {
          this.toastr.error(data.msg, 'Error')
        }
      }, error => this.isLoading = false);
    this.populateSessions(null);
  }  

  populateSubCategoriesII(id) {
    this.cattwos = [];
    this.catthrees = [];
    this.cattwo = null;
    this.catthree = null;
    this.totalSessions = [];
    if (id) {
      this.isLoading = true;
      this.categoryService.getSubCategory(id)
        .subscribe(data => {
          this.isLoading = false;
          if (data.success) {
            this.cattwos = data.category? data.category.childcategories: [];
          } else {
            this.toastr.error(data.msg, 'Error');
          }
        }, error => this.isLoading = false);
      this.populateSessions(id, true);
    }
  }

  populateSubCategoriesIII(id) {
    this.catthrees = [];
    this.totalSessions = [];
    if (id) {
      this.categoryService.getSubCategory(id)
        .subscribe(data => {
          if (data.success) {
            this.catthrees = data.category? data.category.childcategories: [];
          }
        });
      this.populateSessions(id, true);
    }
  }

  populateSessions(categoryId, isParent?) {
    this.totalSessions = [];
    this.qrCodes = [];
    if (categoryId) {
      this.isLoading = true;
      this.imageService.getSessions(categoryId, isParent)
        .subscribe(data => {
          this.isLoading = false;
          if (data.success) {
            this.totalSessions = data.data;
            this.totalSessions.forEach((value, index) => {
              this.qrCodes.push({userId: value.userId, sessionId: value._id, sessionname: value.sessionname, secret: this.qrSecret});
            })
          }
        }, error => this.isLoading = false);
    } else {
      this.imageService.getAllSessions().subscribe(res => {
        if(res.success) {
          this.totalSessions = res.data;
          this.totalSessions.forEach((value, index) => {
            this.qrCodes.push({userId: value.userId, sessionId: value._id, sessionname: value.sessionname, secret: this.qrSecret})
          })
        }
      })
    }
  }

  uuidv4() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
      var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  }  

  removeSession(session, index) {
    this.showConfirm('Confirm Delete', 'Are you sure you want to remove session: ' + session.sessionname).subscribe( res => {
      if(res) {
        this.imageService.removeSession(session).subscribe(res => {
          if(res.success) {
            this.totalSessions.splice(index, 1);
            this.toastr.success(res.msg, 'Success')
          } else {
            this.toastr.error(res.msg, 'Error');
          }
        })
      }
    })   
  }

  removeImage(image, index, images) {
    let id = image._id;
    this.imageService.removeImage(id)
      .subscribe(data => {
        if (data.success) {
          images.splice(index, 1);
        }
      });
  }  

  showImage(session) {
    if (session.images) {
      session.images = null;
      return;
    }
    this.imageService.getImages(session._id)
      .subscribe(data => {
        if (data.success) {
          session.images = data.files;
        }
      });
  }

  dragEnd(images) {
    let items = images;
    for (var index = 0; index < items.length; index++) {
      var element = items[index];
      this.imageService.updateImage(element._id, index)
        .subscribe(data => {
          if (data.success) {
            console.log('Suffeling Successful');
            this.toastr.success('Items reordered', 'Success')
          } else {
            this.toastr.error(data.msg, 'Error');
          }
        });
    }
  }

  catSelect(which, e) {
    if(which == 1) {
      this.catone = e;
      this.populateSubCategoriesII(e);
    } else if(which == 2) {
      this.cattwo = e;
      this.populateSubCategoriesIII(e)
    } else if(which == 3) {
      this.catthree = e;
      this.populateSessions(e);
    }
  }

  startSlideShow(session) {
    this.router.navigate(['/slideshow', session._id]);
  }

  chooseCatOne(catone) {
    this.catone = catone;
    this.populateSubCategoriesII(catone);
  }

  /*
  startSlideShow(session) {
    this.api.fsAPI.toggleFullscreen();

    if (session.images) {
      this.visibleImages = session.images;
      this.slideshow();
    } else {
      this.imageService.getImages(session.sessionname)
        .subscribe(images => {
          if (images.length) {
            this.visibleImages = images;
            this.slideshow();
          }
        });
    }
  }*/

  showConfirm(title, message) {
    let popup = this.dialogService.addDialog(ConfirmPopupComponent, {
      title: title,
      message: message
    })
    return popup;
  }

  public checkInterval;
  public checkSub;
  ngOnInit() {
    // this.imageService.createQrCodes().subscribe(res => {
    //   if(res.success) {
    //     this.qrCodes = res.data;
    //     console.log(this.qrCodes);
    //   } else {
    //     console.log(res.error);
    //   }
    // })

    // this.checkInterval = setInterval(() => {
    //   this.imageService.checkQr().subscribe( res => {
    //     if(res.success) {
    //       this.startSlideShow(res.session);
    //     }
    //   })
    // }, 3000);

    this.checkSub = this.imageService.qrScanCheck().subscribe( data => {
      console.log('qr scan check: ', data);
      if(data.sessionId && data.secret == this.qrSecret) {
        let scannedSession = this.totalSessions.find(session => session._id == data.sessionId)
        this.imageService.qrSecret = this.qrSecret;
        console.log('starting slideshow');
        this.startSlideShow(scannedSession);
      } 
    })
  }

  ngOnDestroy() {
    // this.imageService.removeAllTokens().subscribe( res => {
    //   // callbacks
    // })
    if(this.checkInterval) {
      clearInterval(this.checkInterval);
    }
    if(this.checkSub) {
      this.checkSub.unsubscribe();
    }
  }

}



