import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../shared/auth.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-qr-login',
  templateUrl: './qr-login.component.html',
  styleUrls: ['./qr-login.component.scss']
})
export class QrLoginComponent implements OnInit {
  checkInterval: any;
  loginToken: any;
  timestamp: string;
  checkSub: any;

  JSON = JSON;
  constructor(
    private auth: AuthService,
    private toastr: ToastrService,
    private router: Router
  ) { }

  ngOnInit() {
    this.generateQr();
  }

  ngOnDestroy() {
    if(this.checkInterval) {
      clearInterval(this.checkInterval);
    }
    this.auth.removeAllTokens(this.timestamp).subscribe(res => {
      //
    })
    if(this.checkSub) {
      this.checkSub.unsubscribe();
    }
  }

  generateQr() {
    this.timestamp = Date.now().toString();
    this.auth.generateQr(this.timestamp).subscribe( res => {
      if(res.success) {
        this.loginToken = res.token;
        this.startCheck();
      }
    })
  }

  startCheck() {
    this.checkSub = this.auth.checkQr(this.loginToken._id).subscribe( data => {
      console.log('user-login socket data: ', data);
      if(data.authToken && data.userId) {
        this.auth.getUserById(data.userId).subscribe( res => {
          if(res.success) {
            this.auth.storeUserData(res.user, data.authToken, true);
            this.toastr.success('Login successful!', 'Success');
            this.router.navigate(['session-qr'])
          } else {
            this.toastr.error(res.msg, 'Error')
          }
        })
      }
    })
  }

}



