import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { ToastrService } from 'ngx-toastr';
import { AuthService } from '../shared/auth.service';

import { FormControl, FormGroup, Validators, FormBuilder } from '@angular/forms';

import { PasswordValidation } from '../validator.password';

let strongPassRegex = new RegExp("^(?=.*[A-Z])(?=.*[!@#$&*])(?=.*[0-9])(?=.*[a-z]).{8,}$");

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {

	resetToken: string;
	userId: string;
	tokenIsValid: boolean;
  	constructor(
  				private auth: AuthService,
  				private toastr: ToastrService,
  				private router: Router,
          private route: ActivatedRoute,
          private fb: FormBuilder) { }

  	ngOnInit() {
  		this.route.params.subscribe( params => {
        this.resetToken = params['token'];
        this.auth.verifyResetToken(this.resetToken).subscribe( res => {
          if(res.success) {
            // token is valid
            this.userId = res.userId;
            this.tokenIsValid = true;
          } else {
            // token is invalid
            this.tokenIsValid = false;
            this.toastr.error(res.msg, 'Error');
          }
        })
      })

      this.auth.logout();
  		
  	}

    password: FormControl = new FormControl(null, Validators.compose([Validators.required, Validators.pattern(strongPassRegex), Validators.minLength(8)]));
    confirmPass: FormControl = new FormControl(null, Validators.required);

    resetPassForm: FormGroup = this.fb.group({
      password: this.password,
      confirmPass: this.confirmPass
    }, {
      validator: PasswordValidation.MatchPassword
    })

  	onResetPass() {
  		if(this.resetPassForm.valid) {
        this.resetPassword(this.password.value);
      } else {
        this.toastr.error('Invalid form!', 'Error')
      }
  	}

  	resetPassword(newPass) {
  		this.auth.resetPassword(this.userId, newPass, this.resetToken).subscribe(res => {
  			if(res.success) {
  				this.toastr.success(res.msg, 'Success');
  				this.router.navigate(['/login']);
  			} else {
  				this.toastr.error(res.msg, 'Error');
  			}
  		})
  	}

  checkValid(control: FormControl) {
    if(control.errors && (control.touched || control.dirty)) {
      return 'is-invalid';
    } else if (!control.errors && (control.touched || control.dirty)){
      return 'is-valid';
    }
  }    

}



