import { Component } from '@angular/core';
import { DialogComponent, DialogService } from "ng2-bootstrap-modal";

export interface confirmPopupModal {
	title: string;
	message: string;
}

@Component({
  selector: 'app-confirm-popup',
  templateUrl: './confirm-popup.component.html',
  styleUrls: ['./confirm-popup.component.scss']
})
export class ConfirmPopupComponent extends DialogComponent<confirmPopupModal, any> implements confirmPopupModal {
	title: string;
	message: string;


  constructor(dialogService: DialogService) {
  	super(dialogService);
  }

  confirm() {
  	this.result = true;
  	this.close();
  }
}



