import { Http, Request, RequestOptions, RequestOptionsArgs, Response, XHRBackend } from "@angular/http"
import { Injectable } from "@angular/core"

import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs/Observable';

// operators
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import "rxjs/add/operator/catch";
import { map } from "rxjs/operators/map"

@Injectable()
export class HttpInterceptor extends Http {

    constructor(
        backend: XHRBackend,
        options: RequestOptions,
        public http: Http,
        private toastr: ToastrService
    ) {
        super(backend, options)
    }

    public request(url: string|Request, options?: RequestOptionsArgs): Observable<Response> {
        return super.request(url, options)
            .catch(this.handleError)
    }

    public handleError = (error: Response) => {
            
        // Do messaging and error handling here
        if(error.status == 0){
            this.toastr.error('Cannot connect to server! Please check your connection.', 'Connection Error');
        } else if (error.status == 401) {
            this.toastr.error('You are not authorized. Please check that you are logged in and have proper rights.', 'Unauthorized')
        } else {
            this.toastr.error('Server Error', 'Error');            
        }
        return new ErrorObservable(error)
    }
}



