import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { AuthService } from '../shared/auth.service';
import { ToastrService } from 'ngx-toastr';

import {environment} from '../../environments/environment';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  isLoading: boolean = false;

  captchaKey: string = environment.captchaKey;

  username: FormControl = new FormControl(null, Validators.required);
  password: FormControl = new FormControl(null, Validators.required);
  captcha: FormControl = new FormControl(null);

  logIn: FormGroup = new FormGroup({
    username: this.username,
    password: this.password,
    captcha: this.captcha
  });

  constructor(private authService: AuthService,
              private route: ActivatedRoute,
              private router: Router,
              private auth: AuthService,
              private toastr: ToastrService) { }

  ngOnInit() {
    this.route.params.subscribe( params => {
      if(params['token']) {
        this.auth.confirmEmail(params['token']).subscribe( res => {
          if(res.success) {
            this.toastr.success(res.msg, 'Success');
          } else {
            this.toastr.error(res.msg, 'Error');
          }
        })      
      }
    });    
  }
  onLogin(){
    if(!this.logIn.valid){
      // show errors
      Object.keys(this.logIn.controls).forEach(field => {
        const control = this.logIn.get(field);
        console.log(control);
        control.markAsTouched({ onlySelf: true });
      });
      return;
    }
    let credentials = {
      username : this.username.value,
      password: this.password.value,
      isAdmin: false
    }
    this.isLoading = true;
    this.authService.authenticateUser(credentials)
    .subscribe(data => {
      // this.isLoading = false;
      if(data.success){
        console.log(data);
        this.authService.storeUserData(data.user, data.token);
        this.authService.hasActiveSub().subscribe(res => {
          this.isLoading = false;
          if(res.success) {
            if(res.hasSub) {
              this.router.navigate(['/gallery'])
            } else {
              this.router.navigate(['/pricing'])
            }
          } else {
            this.toastr.error('', 'Error');
          }
        }, error => {
          this.isLoading = false;
        })
      }
      else{
        console.log(data);
        // show error toast
        this.toastr.error(data.msg, 'Error');
      }

    }, error => {
      this.isLoading = false;
    })
  }

  checkValid(control: FormControl) {
    if(control.errors && (control.touched || control.dirty)) {
      return 'is-invalid';
    } else if (!control.errors && (control.touched || control.dirty)){
      return 'is-valid';
    }
  }

}



