import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';

import { AuthService } from '../shared/auth.service';
import { PackageService } from '../shared/package.service';

@Component({
  selector: 'app-select-pack',
  templateUrl: './select-pack.component.html',
  styleUrls: ['./select-pack.component.scss']
})
export class SelectPackComponent implements OnInit {

	packList: any[];
	currPack: any;
	userSubscription: any;
	trialExpires: number;
	registerUserId: string;

	buyBtnText: string = 'Sign up'

	constructor(private packSer: PackageService,
			private toastr: ToastrService,
			private auth: AuthService,
			private router: Router,
			private route: ActivatedRoute) { }

	ngOnInit() {

		if(this.auth.getCurrentUser()){
			this.buyBtnText = 'Purchase';
			this.userSubscription = this.auth.getCurrentUser().subscription;
		}
		this.packSer.listPackages().subscribe( res => {
			if(res.success) {
				this.packList = res.packages;
				let currPackIndex;
				if(this.auth.getCurrentUser() && this.auth.getCurrentUser().subscription)
					currPackIndex = this.packList.findIndex(pack => pack._id == this.auth.getCurrentUser().subscription.packageId);
				if(currPackIndex) {
					this.currPack = this.packList.splice(currPackIndex, 1);
					if(this.userSubscription.isTrial) {
						this.trialExpires = this.currPack.trialPeriod - (Date.now() - this.userSubscription.startDate)/1000/86400;
					}
				}
			} else {
				this.toastr.error(res.msg, 'Error');
			}
		})
	}

	handleTryFree(pack) {
		console.log('Try free: ', pack);
		if(this.auth.loggedIn()) {
			this.auth.changeSubscription(pack, true);
		} else {
			this.router.navigate(['/register'], {queryParams: {pack: pack._id, trial: true}})
		}
	}

	handlePurchase(pack) {
		console.log('Purchase pack: ', pack);
		if(this.auth.loggedIn()) {
			this.auth.changeSubscription(pack, false);
		} else {
			this.router.navigate(['/register'], {queryParams: {pack: pack._id, trial: false}})
		}
	}

	purchaseTrial() {
		console.log('Purchase trial: ', this.currPack);
	}

	cancelSub() {
		console.log('Cancel subsciption: ', this.currPack);
	}

}



