import { Component, OnInit, ViewChild } from '@angular/core';
import { FileUploader, FileUploaderOptions } from 'ng2-file-upload';
import { ImageService } from '../shared/image.service';
import { CategoryService } from '../shared/category.service';

import { Router, ActivatedRoute } from '@angular/router';

import { DialogService } from "ng2-bootstrap-modal";
import { PopupComponent } from '../popup/popup.component';
import { ConfirmPopupComponent } from '../confirm-popup/confirm-popup.component';
import {environment} from '../../environments/environment';
import { ToastrService } from 'ngx-toastr';

import { NgForm, NgModel } from '@angular/forms';


export class Session{
  constructor(
    public _id: string=null,
    public sessionname: string = null,
    public catone: string = null,
    public cattwo: string=null,
    public categoryId: string=null){}
}

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.scss']
})
export class UploadComponent implements OnInit {
  // baseUrl: string = 'http://localhost:8080/';
  // baseUrl: string = 'https://ionic-node-auth.herokuapp.com/';

  editing: boolean = false;

  sessionMedia: any[] = [];
  baseUrl: string = environment.baseUrl;
  uploadUrl: string = this.baseUrl + '/session/upload';
  sessionname: String;

  catones: any[];
  cattwos: any[];
  catthrees: any[];

  currSession: Session = new Session();

  isLoading: boolean = false;

  @ViewChild('f') sessionForm: NgForm;

  public uploader: FileUploader = new FileUploader({ url: this.uploadUrl });

  constructor(private imageService: ImageService,
    private route: ActivatedRoute, 
    private router: Router,
    private categoryService: CategoryService, 
    private dialogService: DialogService,
    private toastr: ToastrService) {
  }

  ngOnInit() {
    this.getRootCategories();
    let routeSub = this.route.params.subscribe(params => {
      if(params['id']) {
        this.editing = true;
        this.isLoading = true;
        setTimeout(() => this.isLoading = false, 3000);
        this.imageService.getSessionById(params['id']).subscribe(res => {
          if(res.success) {
             this.currSession = res.session; // warning: this makes the form dirty
             this.getSessionCategories(res.session.categoryId);
             this.sessionMedia = res.files;
             this.setUploaderCallbacks(this.currSession);
          } else {
            this.toastr.error(res.msg, 'Error')
          }
        })
      }
    })
  }

  getSessionCategories(catId) {
    this.categoryService.getSessionCats(catId).subscribe( res => {
      if(res.success) {
        //get all the categories regarding the session and the selected ones
        this.catthrees = res.cats.catthrees;
        this.cattwos = res.cats.cattwos;
        this.currSession.catone = res.cats.catone;
        this.currSession.cattwo = res.cats.cattwo;
      }
    })
  }

  getRootCategories() {
    this.categoryService.getRootCategory()
      .subscribe(data => {
        if (data.success) {
          this.catones = data.categories;
        } else {
          this.toastr.error(data.msg, 'Error');
        }
      });
  }

  populateSubCategoriesII(id) {
    this.cattwos = [];
    this.catthrees = [];
    if (id) {
      let selectedCat = this.catones.find(val => val._id === id);
      this.cattwos = selectedCat? selectedCat.childcategories : [];
    }
  }

  populateSubCategoriesIII(id) {
    this.catthrees = [];
    if (id) {
      this.isLoading = true;
      this.categoryService.getSubCategory(id)
        .subscribe(data => {
          this.isLoading = false;
          if (data.success) {
            this.catthrees = data.category? data.category.childcategories: [];
          } else {
            this.toastr.error(data.msg, 'Error');
          }
        }, error => {
          this.isLoading = false;
        });
    }
  }

  removeSession() {
    this.showConfirm('Confirm Delete', 'Are you sure you want to remove session: ' + this.currSession.sessionname).subscribe( res => {
      if(res) {
        this.imageService.removeSession(this.currSession).subscribe(res => {
          if(res.success) {
            this.toastr.success(res.msg, 'Success')
            this.router.navigate(['/gallery'])
          } else {
            this.toastr.error(res.msg, 'Error')
          }
        })
      }
    })   
  }

  catSelect(which, e) {
    if(which == 1) {
      this.currSession.catone = e;
      this.populateSubCategoriesII(e);
    } else if(which == 2) {
      this.currSession.cattwo = e;
      this.populateSubCategoriesIII(e)
    } else if(which == 3) {
      this.currSession.categoryId = e;
    }
  }

  editMediaInfoModal(file, index?) {
    console.log('edit meidainfo modal');
    this.showDelayPopup(file).subscribe(data => {
      if(data) {
        this.imageService.updateMedia(file._id, {titleAlign: data.titleAlign, titleSize: data.titleSize})
          .subscribe(res => {
            if(res.success) {
              this.toastr.success('Media updated successfully!', 'Success');
              let mediaFile = this.sessionMedia.find(media => media._id === file._id);
              mediaFile.titleAlign = data.titleAlign;
              mediaFile.titleSize = data.titleSize;
            } else {
              this.toastr.error(res.msg, 'Error');
            }
          })
      }
      if(data && data.delay) {
        this.imageService.updateDelay(file._id, data.delay).subscribe( res => {
          if(res.success) {
            //success
            this.toastr.success('Delay Updated', 'Success');
            this.sessionMedia.find(media => media._id === file._id).imagedelay = data.delay;
          } else {
             //error
             this.toastr.error(res.msg, 'Error');
          }
        })
      }
      if( data && data.title) {
        this.imageService.updateTitle(file._id, data.title).subscribe(res => {
          if(res.success) {
            //success
            if(!index){
              //when index is not provided it is the last element
              let index = this.sessionMedia.length;
            }
            this.sessionMedia[index].imagetitle = data.title;
            this.toastr.success('Title updated', 'Success');
          } else {
            //error
            this.toastr.error(res.msg, 'Error');
          }
        })
      }      
    })
  }

  setUploaderCallbacks(session) {
    var uo: FileUploaderOptions = {};
    uo.additionalParameter = { sessionId: session._id };
    uo.itemAlias = 'upload_file';
    uo.headers = [{name: 'Authorization', value: localStorage.getItem('id_token')}];
    this.uploader.setOptions(uo);

    console.log(this.uploader.options);

    this.uploader.onBeforeUploadItem = (fileItem: any) => {
      // console.log('starting upload item: '+ fileItem.file.name);
    };

    this.uploader.onAfterAddingAll = (items) => {
      console.log(items);
    }

    this.uploader.onErrorItem = (item, response, status, headers) => {
      //show errors
      console.error('File uploader status code: ', status);
      if(status == 0) {
        this.toastr.error('Cannot connect to server! Make sure you have active internet connection.', 'Error');
      } else {
        let res = JSON.parse(response);
        this.toastr.error(res.msg, 'Error');
      }

    }

    this.uploader.onSuccessItem = (item, response, status, headers) => {
      this.sessionMedia.push(JSON.parse(response).file);
      if(item.file.type.indexOf('image/') !== -1) {
        let delaySub = this.showDelayPopup(JSON.parse(response).file).subscribe(data => {
          console.log(data);
          if(data) {
            this.imageService.updateMedia(JSON.parse(response).file._id, {titleAlign: data.titleAlign, titleSize: data.titleSize})
              .subscribe(res => {
                if(res.success) {
                  this.toastr.success('Media updated successfull', 'Success');
                  let updateFile = this.sessionMedia.find(media => media._id === JSON.parse(response).file._id);
                  updateFile.titleAlign = data.titleAlign,
                  updateFile.titleSize = data.titleSize
                } else {
                  this.toastr.error(res.msg, 'Error')
                }
              })
          }
          if(data && data.delay) {
            this.imageService.updateDelay(JSON.parse(response).file._id, data.delay).subscribe( res => {
              if(res.success) {
                //success
                this.toastr.success(res.msg, 'Success')
              } else {
                 //error
                 this.toastr.error(res.msg, 'Error')
              }
            })
          }
          if( data && data.title) {
            this.imageService.updateTitle(JSON.parse(response).file._id, data.title).subscribe(res => {
              if(res.success) {
                //success
                this.toastr.success(res.msg, 'Success')

              } else {
                //error
                 this.toastr.error(res.msg, 'Error')

              }
            })
          }
        })
      }
    }
  }

  updateSession(form, editing) {
    if(form.invalid) {
      this.toastr.error('Please fill the complete form', 'Error')
      return;   
    }
    if(!editing) return this.createNewSession(form);
    let updatedSession = {
      _id: this.currSession._id,
      sessionname: this.currSession.sessionname,
      categoryId: this.currSession.categoryId
    };    
    this.imageService.updateSession(updatedSession)
      .subscribe(res => {
        if(res.success) {
          this.toastr.success('Session updated successfully!', 'Success');
        } else {
          this.toastr.error(res.msg, 'Error')
        }
      })
  }

  removeImage(image, index) {
    let id = image._id;
    this.imageService.removeImage(id)
      .subscribe(data => {
        if (data.success) {
          this.sessionMedia.splice(index, 1);
          this.toastr.success('Media file removed', 'Success');
        } else {
          this.toastr.error(data.msg, 'Error')
        }
      });
  }



  createNewSession(form) {
    let sessionname = this.currSession.sessionname;
    if (this.currSession.categoryId) {
      this.imageService.createSessions(sessionname, this.currSession.categoryId)

        .subscribe(data => {
          if (data.success) {
            this.sessionname = null;
            form.reset();
            this.toastr.success('Session created successfully!', 'Success')
            this.router.navigate(['/edit-session', data.session._id])
          } else {
            this.toastr.error(data.msg, 'Error');
          }
        });
    }
  };

  showDelayPopup(file) {
      let disposable = this.dialogService.addDialog(PopupComponent, {
          title:'Enter title and delay for image',
          message:'Enter Delay for image: '+file.imagetitle,
          imgTitle: file.imagetitle,
          delay: file.imagedelay,
          titleSize: file.titleSize,
          titleAlign: file.titleAlign
        })

      return disposable;
  }

  showConfirm(title, message) {
    let popup = this.dialogService.addDialog(ConfirmPopupComponent, {
      title: title,
      message: message
    })
    return popup;
  }

  changeSessionTab(e) {
    console.log('tab change', e);
    if(e.nextId === 'edit_media') {
      if(this.currSession) {
        this.setUploaderCallbacks(this.currSession);
      } else {
        // show feedback to save session first
        e.preventDefault();
      }
    }
  }

  dragEnd(images) {
    console.log('drag end: ', images);
    let items = images;
    for (var index = 0; index < items.length; index++) {
      var element = items[index];
      this.imageService.updateImage(element._id, index)
        .subscribe(data => {
          if (data.success) {
            this.toastr.success('Items reordered', 'Success')
          } else {
            this.toastr.error(data.msg, 'Error');
          }
        });
    }
  }

  getImage(file) {
    if(file.imagetype == 'image') {
      return this.baseUrl+'/session/stream_files?file='+file.imagename;
    }
  }

  getThumb(file) {
    return this.baseUrl+'/session/stream_files?file='+file.imagethumb;
  }

  checkValid(control) {
    if(control.errors && (control.touched || control.dirty)) {
      return 'is-invalid';
    } else if (!control.errors && (control.touched || control.dirty)){
      return 'is-valid';
    }
  }  

  ngOnDestroy() {
  }
}



