import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import {NgbModule} from '@ng-bootstrap/ng-bootstrap';

import { HttpModule } from '@angular/http';
import { HttpInterceptor } from './shared/http-interceptor';
import { FileSelectDirective } from 'ng2-file-upload';
import { RouterModule, Routes } from '@angular/router';
import { AlertModule } from 'ngx-bootstrap';
import { VgCoreModule } from 'videogular2/core';
import { DndModule } from 'ng2-dnd';
import { QRCodeModule } from 'angular2-qrcode';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { ToastrModule } from 'ngx-toastr';

import { SocketIoModule, SocketIoConfig } from 'ng-socket-io';

import { BootstrapModalModule } from 'ng2-bootstrap-modal';

import {IonCaptchaDirective} from './shared/ionCaptcha.directive';

import { LoadingModule } from 'ngx-loading';

import {FlashMessagesModule} from 'angular2-flash-messages';

import { ImageService } from './shared/image.service';
import { CategoryService } from './shared/category.service';
import { AuthService } from './shared/auth.service';
import { PackageService } from './shared/package.service';

import { AuthGuard } from './shared/auth.guard';
import { LoginGuard } from './shared/login.guard';
import { AdminGuard } from './shared/admin.guard';
import { PackGuard } from './shared/pack.guard';

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { SlideshowComponent } from './slideshow/slideshow.component';
import { HeaderComponent } from './header/header.component';
import { GalleryComponent } from './gallery/gallery.component';
import { AdminComponent } from './admin/admin.component';
import { ImageComponent } from './image/image.component';
import { UploadComponent } from './upload/upload.component';
import { RegisterComponent } from './register/register.component';
import { PopupComponent } from './popup/popup.component';
import { SessionQrComponent } from './session-qr/session-qr.component';
import { QrLoginComponent } from './qr-login/qr-login.component';
import { ResetPasswordComponent } from './reset-password/reset-password.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { ConfirmPopupComponent } from './confirm-popup/confirm-popup.component';
import { AdminLoginComponent } from './admin-login/admin-login.component';
import { PackageComponent } from './package/package.component';
import { SelectPackComponent } from './select-pack/select-pack.component';

import { UsernameValidator } from './validator.username';

import {environment} from '../environments/environment';

const ioConfig: SocketIoConfig = {url: environment.socketUrl, options: {}};

// const loadingConfig = {}

const ROUTES = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  },
  {
    path: 'login',
    component: LoginComponent,
    canActivate: [LoginGuard]
  },
  {
    path: 'login/:token',
    component: LoginComponent
  },
  {
    path: 'qr-login',
    component: QrLoginComponent
  },
  {
    path: 'register',
    component: RegisterComponent
  },
  {
    path: 'forgot_password',
    component: ForgotPasswordComponent
  },
  {
    path: 'reset_password/:token',
    component: ResetPasswordComponent
  },
  {
    path: 'images',
    component: ImageComponent
  },
  {
    path: 'gallery',
    component: GalleryComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'slideshow/:id',
    component: SlideshowComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'session-qr',
    component: SessionQrComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'create',
    component: UploadComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'edit-session/:id',
    component: UploadComponent,
    canActive: [AuthGuard]
  },
  {
    path: 'admin',
    component: AdminComponent,
    canActivate: [AuthGuard, AdminGuard]
  }, 
  {
    path: 'admin-login',
    component: AdminLoginComponent
  },
  {
    path: 'add-package',
    component: PackageComponent,
    canActivate: [AdminGuard]
  },
  {
    path: 'edit-package/:id',
    component: PackageComponent,
    canActivate: [AdminGuard]
  },
  {
    path: 'pricing',
    component: SelectPackComponent,
    canActivate: [PackGuard]
  }
];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    HeaderComponent,
    GalleryComponent,
    SlideshowComponent,
    ImageComponent,
    UploadComponent,
    AdminComponent,
    FileSelectDirective,
    RegisterComponent,
    PopupComponent,
    IonCaptchaDirective,
    SessionQrComponent,
    QrLoginComponent,
    ResetPasswordComponent,
    ForgotPasswordComponent,
    ConfirmPopupComponent,
    AdminLoginComponent,
    PackageComponent,
    SelectPackComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    NgbModule.forRoot(),
    HttpModule,
    VgCoreModule,
    QRCodeModule,
    AlertModule.forRoot(),
    RouterModule.forRoot(ROUTES),
    DndModule.forRoot(),
    FlashMessagesModule,
    BrowserAnimationsModule,
    ToastrModule.forRoot({
      preventDuplicates: true
    }),
    BootstrapModalModule.forRoot({container:document.body}),
    SocketIoModule.forRoot(ioConfig),
    LoadingModule
  ],
  entryComponents: [
    PopupComponent,
    ConfirmPopupComponent
  ],
  providers: [
    ImageService,
    AuthService,
    PackageService,
    CategoryService,
    AuthGuard,
    LoginGuard,
    AdminGuard,
    PackGuard,
    HttpInterceptor,
    UsernameValidator
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



