import { Component } from '@angular/core';
import { DialogComponent, DialogService } from "ng2-bootstrap-modal";
export interface PopupModal {
  title:string;
  message:string;
  filename: string;
  delay: number;
  imgTitle: string;
  titleSize: number;
  titleAlign: string;
}

@Component({
    selector: 'popup',
    styleUrls: ['./popup.component.scss'],
    template: `<div class="modal-dialog">
                <div class="modal-content">
                   <div class="modal-header">
                      <h4 class="modal-title">{{title || 'Confirm'}}</h4>
                      <button type="button" class="close" (click)="close()" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                      </button>                     
                   </div>
                   <div class="modal-body">
                     <p>{{message || 'Edit title and delay for'+filename}}</p>
                     <input class="form-control" placeholder="Delay (in seconds)" type="number" [(ngModel)]="delay"/>
                     <br>
                     <input class="form-control" placeholder="Title" type="text" [(ngModel)]="imgTitle"/>
                     <br>
                     <label>Title font size</label>
                      <select class="custom-select form-control mb-2" placeholder="Title font size" [(ngModel)]="titleSize">
                        <option value="16">16px</option>
                        <option value="18">18px</option>
                        <option value="20">20px</option>
                        <option value="24">24px</option>
                        <option value="28">28px</option>
                      </select>
                      <label>Title font alignment</label>
                      <select class="form-control mb-2 custom-select" [(ngModel)]="titleAlign">
                         <option value="tl">Top left</option>
                         <option value="bl">Bottom left</option>
                         <option value="tr">Top Right</option>
                         <option value="br">Bottom Right</option>
                      </select>
                   </div>
                   <div class="modal-footer">
                     <button type="button" class="btn btn-primary" (click)="confirm()">OK</button>
                     <button type="button" class="btn btn-default" (click)="close()" >Cancel</button>
                   </div>
                 </div>
              </div>`
})
export class PopupComponent extends DialogComponent<PopupModal, any> implements PopupModal {
  title: string;
  message: string;
  filename: string;
  delay: number;
  imgTitle: string;
  titleSize: number;
  titleAlign: string;
  
  constructor(dialogService: DialogService) {
    super(dialogService);
  }
  confirm() {
    this.result = {
      delay: this.delay,
      title: this.imgTitle,
      titleSize: this.titleSize,
      titleAlign: this.titleAlign
    }
    this.close();
  }
}



