import { Injectable } from '@angular/core';
import { Headers } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { tokenNotExpired } from 'angular2-jwt';
import { Socket } from 'ng-socket-io';

import { HttpInterceptor } from './http-interceptor';

import {environment} from '../../environments/environment';

import {map} from 'rxjs/operators/map';

@Injectable()
export class AuthService {
  headers: Headers = new Headers();
  authToken: any = localStorage.getItem('id_token') || null;
  currentUser: any = JSON.parse(localStorage.getItem('user')) || null;
  isScanned: boolean = this.currentUser? this.currentUser.isScanned : null;

  authUrl = environment.baseUrl +'/users/';
  constructor(private http: HttpInterceptor, private socket: Socket) {
    this.headers.append('Content-type', 'application/json');
    this.headers.append('Authorization', this.getAuthToken());
  }

  authenticateUser(creds) {
    return this.http.post(this.authUrl + 'authenticate', creds, { headers: this.headers })
      .map(
      res => res.json(),
      err => console.log(err)
      );
  }

  registerUser(credentials) {
    return this.http.post(this.authUrl + 'register', credentials, { headers: this.headers })
      .map(
      res => res.json(),
      err => console.log(err)
      );
  }

  validateUsername(username) {
    return this.http.post(this.authUrl+'check_username', {username: username})
      .map(res => res.json());
  }

  storeUserData(user, token, isScanned?) {
    localStorage.setItem('user', JSON.stringify(user));
    localStorage.setItem('id_token', token);
    this.authToken = token;
    this.currentUser = user;
    this.isScanned = isScanned? true: false;
    console.log(this.currentUser);
  }

  getCurrentUser() {
    return JSON.parse(localStorage.getItem('user'));
  }

  getAuthToken() {
    return localStorage.getItem('id_token');
  }

  getUserById(userId) {
    return this.http.post(this.authUrl+'getUserById', {userId: userId}).map(res => res.json());
  }

  hasActiveSub() {
    if(this.currentUser.subscription && this.currentUser.subscription.packageId) {
      // return this.http.get(this.authUrl+'checkSubscription', {headers: this.headers})
      //   .map(res => res.json());
      return Observable.of({success: true, hasSub: true})
    }
    return Observable.of({success: true, hasSub: false});
  }

  changeSubscription(packId, isTrial) {
    return this.http.post(this.authUrl+'/changeSubscription', {packageId: packId, isTrial: isTrial}, {headers: this.headers})
      .map(res => res.json());
  }

  scannedLogIn() {
    return this.isScanned;
  }

  getUserId() {
    return JSON.parse(localStorage.getItem('user')).id;
  }

  loggedIn() {
    return tokenNotExpired('id_token');
  }

  adminLogIn() {
    return this.currentUser? this.currentUser.isAdmin: false;
  }

  customerLogin() {
    return this.loggedIn() && !this.adminLogIn();
  }

  generateQr(timestamp) {
    return this.http.post(this.authUrl + 'generate-qr', {timestamp: timestamp})
        .map(
          res => res.json(),
          err => {
            console.log(err);
          }
        );
  }

  // checkQr(tokenId) {
  //   return this.http.post(this.authUrl + 'check-qr', {tokenId: tokenId})
  //       .map(
  //         res => res.json(),
  //         err => {
  //           console.log(err);
  //         }
  //       );
  // }

  checkQr(tokenId) {
    return this.socket.fromEvent<any>('user-login');
  }

  removeAllTokens(timestamp) {
    return this.http.post(this.authUrl+'remove-qr', {timestamp: timestamp})
        .map(
          res => res.json(),
          err => {
            console.log(err);
          }
        )
  }

  verifyResetToken (token: string) {
    return this.http.post(this.authUrl+'verify-reset-token', {token: token})
        .map(
          res => res.json());
  }

  sendResetLink(user) {
    return this.http.post(this.authUrl+'forgot_password', {user: user}).map(res => res.json());
  }

  resetPassword(userId, newPass, token) {
    return this.http.post(this.authUrl+'reset_password', {newPass: newPass, token: token, userId: userId}).map(
      res => res.json());
  }

  confirmEmail(token: string) {
    return this.http.post(this.authUrl+'confirm_email', {token: token}).map(res => res.json());
  }

  logout() {
    this.authToken = null;
    this.currentUser = null;
    localStorage.clear();
  }

}



