import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

import { AuthService } from '../shared/auth.service';

import { ToastrService } from 'ngx-toastr';

import { environment } from '../../environments/environment';

@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.scss']
})
export class AdminLoginComponent implements OnInit {

  captchaKey: string = environment.captchaKey;

  username: FormControl = new FormControl(null, Validators.required);
  password: FormControl = new FormControl(null, Validators.required);
  captcha: FormControl = new FormControl(null);

  adminLoginForm: FormGroup = new FormGroup({
    username: this.username,
    password: this.password,
    captcha: this.captcha
  });

  constructor(private auth: AuthService,
  	private router: Router,
    private toastr: ToastrService) { }

  ngOnInit() {
  }

  onLogin() {
  	if(this.adminLoginForm.valid) {
  		let credentials = {
  			username: this.username.value,
  			password: this.password.value,
  			isAdmin: true
  		};

  		this.auth.authenticateUser(credentials)
  			.subscribe( res => {
  				if(res.success) {
  					this.router.navigate(['/admin'])
  					this.auth.storeUserData(res.user, res.token);
  				} else {
  					// show error toast
            this.toastr.error(res.msg, 'Error');
  				}
  			})

  	} else {
  		// show errors
  		Object.keys(this.adminLoginForm.controls).forEach(field => {
		  const control = this.adminLoginForm.get(field);
		  control.markAsTouched({ onlySelf: true });
		});
  	}
  }

  checkValid(control: FormControl) {
    if(control.errors && (control.touched || control.dirty)) {
      return 'is-invalid';
    } else if (!control.errors && (control.touched || control.dirty)){
      return 'is-valid';
    }
  }  

}



