import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';

import { ToastrService } from 'ngx-toastr';

import { PackageService } from '../shared/package.service';

@Component({
  selector: 'app-package',
  templateUrl: './package.component.html',
  styleUrls: ['./package.component.scss']
})
export class PackageComponent implements OnInit {
	isLoading: boolean = false;
	packEdit: boolean = false;
	currPack: any;

	currencies: string[];
	storage_units_list: string[];

	packForm: FormGroup;

	name: FormControl = new FormControl(null, Validators.required);
	monthly_pricing: FormControl = new FormControl(null, Validators.required)
	yearly_pricing: FormControl = new FormControl(null)
	currency: FormControl = new FormControl(null, Validators.required);
	details: FormControl = new FormControl(null);
	storage_amount: FormControl = new FormControl(null, Validators.required);
	storage_unit: FormControl = new FormControl(null, Validators.required)
	trialPeriod: FormControl = new FormControl(15)

	constructor(private packSer: PackageService,
			private router: Router,
			private route: ActivatedRoute,
			private toastr: ToastrService,
			private fb: FormBuilder) {
	}

	ngOnInit() {
		this.currencies = ['USD', 'CAD', 'GBP', 'INR', 'EUR', 'YEN'];
		this.storage_units_list = ['Mb', 'Gb', 'MB', 'GB'];
		this.route.params.subscribe( params => {
			if(params['id']) {
				this.initEditPack(params['id']);
			}
		})

		this.packForm = this.fb.group({
			name: this.name,
			pricing: this.fb.group({
				monthly: this.fb.group({
					pay: this.monthly_pricing,
				}),
				annually: this.fb.group({
					pay: this.yearly_pricing,
				}),
				currency: this.currency
			}),
			storage: this.fb.group({
				amount: this.storage_amount,
				unit: this.storage_unit
			}),
			details: this.details,			
			trialPeriod: this.trialPeriod
		});
	}

	initEditPack(packId) {
		this.packEdit = true;
		this.isLoading = true;
		this.packSer.getPackage(packId).subscribe( res => {
			this.isLoading = false;
			if(res.success) {
				this.currPack = res.package;
				this.setFormValues(res.package);
			} else {
				this.toastr.error(res.msg, 'Error')
			}
		}, error => {
			this.isLoading = false;
		})
	}

	setFormValues(pack) {
		this.packForm.setValue({
			name: pack.name,
			pricing: pack.pricing,
			storage: pack.storage,
			details: pack.details,
			trialPeriod: pack.trialPeriod
		}, {
			emitEvent: false
		})
	}

	submitPackForm() {
		if(this.packForm.invalid) {
	      // show errors
	      Object.keys(this.packForm.controls).forEach(field => {
	        const control = this.packForm.get(field);
	        console.log(control);
	        control.markAsTouched({ onlySelf: true });
	      });
	      return;			
		}

		if(this.packEdit) {
			this.updatePackage();
		} else {
			this.addNewPackage();
		}
	}

	updatePackage() {
		this.isLoading = true;
		this.packSer.updatePackage(this.currPack._id, this.packForm.value).subscribe(res => {
			this.isLoading = false;
			if(res.success) {
				this.currPack = res.package
				this.toastr.success('Package updated', 'Success')
			} else {
				this.toastr.error(res.msg, 'Error')
			}
		}, error => {
			this.isLoading = false;
		})
	}

	addNewPackage() {
		this.isLoading = true;
		this.packSer.addPackage(this.packForm.value).subscribe( res => {
			this.isLoading = false;
			if(res.success) {
				this.packForm.reset();
				this.toastr.success('New package added!', 'Success')
				this.currPack = null;
			} else {
				this.toastr.error(res.msg, 'Error');
			}
		}, error => {
			this.isLoading = false;
		})
	}

	floatify(control: FormControl) {
		control.setValue(control.value.toFixed(2), {
			emitEvent: false
		});
	}

	checkValid(control: FormControl) {
		if(control.errors && (control.touched || control.dirty)) {
		  return 'is-invalid';
		} else if (!control.errors && (control.touched || control.dirty)){
		  return 'is-valid';
		}
	}

}



