import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

import { AuthService } from '../shared/auth.service';
import { UsernameValidator } from '../validator.username';
import { PasswordValidation } from '../validator.password';

import { environment } from '../../environments/environment';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  captchaKey: string = environment.captchaKey;

  registerForm: FormGroup;
  username: FormControl;
  password: FormControl;
  name: FormControl;
  email: FormControl;
  confirmPass: FormControl;
  captcha: FormControl;

  userSubscription: any;

  constructor(private authService: AuthService,
              private router: Router,
              private toastr: ToastrService,
              private usernameValidator: UsernameValidator,
              private fb: FormBuilder,
              private route: ActivatedRoute) {
    let strongPassRegex = new RegExp("^(?=.*[A-Z])(?=.*[!@#$&*])(?=.*[0-9])(?=.*[a-z]).{8,}$");

    this.username = new FormControl(null, Validators.required, usernameValidator.checkUsername.bind(usernameValidator));
    this.password = new FormControl(null, Validators.compose([Validators.required, Validators.pattern(strongPassRegex), Validators.minLength(8)]));
    this.name = new FormControl(null, Validators.required);
    this.email = new FormControl(null, Validators.compose([Validators.required, Validators.email]));
    this.confirmPass = new FormControl(null, Validators.required)
    this.captcha = new FormControl(null);
    
    this.registerForm = fb.group({
      username: this.username,
      email: this.email,
      password: this.password,
      name: this.name,
      confirmPass: this.confirmPass,
      captcha: this.captcha
    }, {
      validator: PasswordValidation.MatchPassword
    });    
  }

  ngOnInit() {
    this.route.queryParamMap.subscribe(params => {
      this.userSubscription = {
        packageId:  params.get('pack'),
        isTrial: params.get('trial')
      };
    })
  }

  onRegister(){
    if(!this.registerForm.valid) return; //show error
    let credentials = {
      username: this.registerForm.value.username,
      email: this.registerForm.value.email,
      password: this.registerForm.value.password,
      name: this.registerForm.value.name,
      subscription: this.userSubscription
    };

    this.authService.registerUser(credentials)

    .subscribe(data => {
      if(data.success){
        this.toastr.success(data.msg, 'Success');
        this.router.navigate(['login']);
      } else {
        this.toastr.error(data.msg, 'Error');
      }
    });
   }

  checkValid(control: FormControl) {
    if(control.errors && (control.touched || control.dirty)) {
      return 'is-invalid';
    } else if (!control.errors && (control.touched || control.dirty)){
      return 'is-valid';
    }
  }  

}



